@import url(https://fonts.googleapis.com/css?family=Exo+2:300,400&display=swap);
:root {
  --background: rgb(40, 45, 50);
  --btn: rgb(10, 15, 20);
  --btn-hover: rgb(29, 36, 44);
  --btn-active: rgb(0, 0, 0);
  --black: rgb(0, 5, 10);
  --input-placeholder: rgb(112, 115, 117);
  --input-background: rgb(31, 34, 37);
  --white: rgb(210, 220, 230);
  --white-disabled: rgb(141, 145, 148);
  --red: rgb(204, 109, 121);
  --red-darker: rgb(119, 63, 71);
  --blue: rgb(76, 106, 145);
  --blue-darker: rgb(46, 64, 87);
  --green-darker: rgb(51, 71, 59);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
  padding-top: 20px;
}

body, input, button {
  font-family: 'Exo 2', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(40, 45, 50);
  background-color: var(--background);
  color: rgb(210, 220, 230);
  color: var(--white);
  font-size: 18px;
}

input::-webkit-input-placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input:-ms-input-placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input::-ms-input-placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input::placeholder {
  color: rgb(112, 115, 117);
  color: var(--input-placeholder);
}

input {
  border: 1px solid rgb(112, 115, 117);
  border: 1px solid var(--input-placeholder);
  padding: 5px 10px;
  background-color: rgb(31, 34, 37);
  background-color: var(--input-background);
  border-radius: 2px;
}

ul {
  list-style: none;
}

button, .btn{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 200px;
  height: 50px;
  border: none;
  background-color: rgb(10, 15, 20);
  background-color: var(--btn);
  color: rgb(210, 220, 230);
  color: var(--white);
  font-size: 18px;
  cursor: pointer;
  transition: .2s;
  border-radius: 2px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

button:hover, .btn:hover {
  background-color: rgb(29, 36, 44);
  background-color: var(--btn-hover);
}

button:active, .btn:active {
  background-color: rgb(0, 0, 0);
  background-color: var(--btn-active);
  border: none;
  box-shadow: none;
  outline: none;
}
.task-list {
	padding: 0 10px;
	box-shadow: 0px 3px 30px -15px black;
}
.task-list-item {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 10px 0;
	padding-left: 10px;
	border-bottom: 1px solid var(--black);
}

.task-list-item:last-child {
	border-bottom: none;
}

.task-info {
	margin-left: auto;
	margin-right: 10px;
}

.task-info-item {
	font-size: 16px;
	color: var(--white-disabled);
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}
.task-info-item p:first-child {
	margin-right: 10px;
}

.task-create-type-choise {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 20px;
}

.task-title {
	font-size: 24px;
}

.task-duration {
	display: flex;
	align-items: center;
	justify-content: space-around;
	font-size: 20px;
}

.task-create-type-choise .btn {
	margin-left: 5px;
	width: auto;
	padding: 0 20px;
	overflow: hidden;
	position: relative;
}

.delete-task {
	width: 50px;
	position: relative;
	padding: 0;
}

.delete-task img {
	position: absolute;
	height: 70%;
	opacity: 0.7;
	transition: .2s;
}

.delete-task:hover img  {
	height: 75%;
	opacity: 0.6;
}

.task-create-type-choise .btn img{
	position: absolute;
	height: 100%;
	opacity: .1;
	right: 10px;
}

.task-create-type-choise .btn:hover img {
	height: 150%;
	right: -5px;
	opacity: .05;
	transition: all .2s ease, -webkit-transform 10s linear;
	transition: all .2s ease, transform 10s linear;
	transition: all .2s ease, transform 10s linear, -webkit-transform 10s linear;
	-webkit-transform: rotateZ(1000deg);
	        transform: rotateZ(1000deg);
}

.addTask-wrap {
	box-shadow: 0px 3px 30px -15px black;
	padding: 10px;
	margin-bottom: 20px;
}

.task-create-title {
	font-size: 24px;
	margin-bottom: 20px;
	width: 100%;
}

.task-create-btn {
	width: 100%;
	height: 60px;
	font-size: 22px;
}

.task-create-time {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 20px;
}

.task-create-time input {
	font-size: 16px;
	margin: 0 10px;
}
.salary-calc-wrap {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	align-items: center;
	justify-content: space-around;
	top: 0;
	z-index: 15;
}

.salary-calc-close-btn {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-around;
	margin-left: auto;
	cursor: pointer;
}

.salary-calc-close-btn img {
	position: absolute;
	right: -65px;
	top: -65px;
	width: 30px;
	height: 30px;
	transition: .2s;
	opacity: .8;
}

.salary-calc-close-btn:hover img{
	opacity: 1;
}

.salary-calc-form {
	padding: 30px;
	background-color: var(--background);
}

.salary-rate-wrap {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	margin-bottom: 30px;
}

.salary-rate-wrap input {
	width: 60px;
	margin: 0 5px;
}

.salary-calc-option label{
	display: flex;
	align-items: center;
	flex-direction: row;
	margin-bottom: 20px;
}

.salary-calc-option label input {
	margin-right: 10px;
}

.salary-calc-date {
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 20px;
}

.salary-calc-btn {
	margin: 0 auto;
	margin-bottom: 20px;
}

.salary-calc-result {
	font-size: 22px;
	text-align: center;
}
.datePicker input{
	background-color: var(--background);
	cursor: pointer;
	transition: .2s;
}

.datePicker input:hover {
	background-color: var(--input-placeholder);
}

#datePicker{
	font-size: 24px;
	border: none;
}
.main-header {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 3px 30px -15px black;
	height: 60px;
	margin-bottom: 20px;
	position: relative;
	padding: 35px 0;
}

.salary-calc-show-btn {
	position: absolute;
	right: 10px;
}
